/* FindingBar - Premium Dark Theme Styles */

/* ===== CSS VARIABLES ===== */
:root {
  /* Backgrounds - Deep Blue-Black Gradient Theme */
  --bg-primary: #000000;
  --bg-secondary: #0a0e27;
  --bg-tertiary: #121212;
  --bg-overlay: rgba(255, 255, 255, 0.1);
  
  /* Text - High Contrast */
  --text-primary: #FFFFFF;
  --text-secondary: rgba(255, 255, 255, 0.85);
  --text-muted: #4D4D4D;
  --text-silver: #C0C0C0;
  
  /* Borders */
  --border-subtle: rgba(255, 255, 255, 0.25);
  --border-medium: rgba(255, 255, 255, 0.4);
  
  /* Brand Colors */
  --brand-primary: #00FFD1;
  --brand-hover: rgba(0, 255, 209, 0.1);
  --brand-active: #6FD2C0;
  --whatsapp-green: #25D366;
  
  /* Spacing */
  --spacing-xs: 8px;
  --spacing-sm: 16px;
  --spacing-md: 24px;
  --spacing-lg: 40px;
  --spacing-xl: 60px;
  --spacing-2xl: 100px;
  --spacing-3xl: 160px;
  
  /* Transitions */
  --transition-smooth: all 0.4s ease-in-out;
  --transition-fast: all 0.2s ease;
}

/* ===== GLOBAL RESET ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
  background: linear-gradient(180deg, #000000 0%, #0a0e27 50%, #000000 100%);
  color: var(--text-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

/* ===== TYPOGRAPHY ===== */
.display-huge {
  font-size: 66px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: -0.62px;
}

.display-large {
  font-size: 48px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: -0.02em;
}

.display-medium {
  font-size: 32px;
  font-weight: 600;
  line-height: 1.25;
  letter-spacing: -0.01em;
}

.heading-1 {
  font-size: 28px;
  font-weight: 600;
  line-height: 1.2;
}

.heading-2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.3;
}

.heading-3 {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.4;
}

.body-large {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.6;
  color: var(--text-primary);
}

.body-medium {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  color: var(--text-secondary);
}

.body-small {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  color: var(--text-secondary);
}

.body-muted {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  color: var(--text-muted);
}

/* ===== DARK CONTAINER ===== */
.dark-container {
  background: linear-gradient(180deg, #000000 0%, #0a0e27 50%, #000000 100%);
  position: relative;
  min-height: 100vh;
  padding-top: 80px;
}

/* ===== HEADER ===== */
.dark-header {
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(12px);
  border-bottom: 1px solid var(--border-subtle);
  padding: 0 7.6923%;
  position: fixed;
  top: 0;
  width: 100%;
  height: 80px;
  z-index: 1000;
  transition: var(--transition-smooth);
}

.dark-header.header-scrolled {
  background: rgba(0, 0, 0, 0.98);
  border-bottom: 1px solid var(--border-medium);
  box-shadow: 0 4px 24px rgba(0, 255, 209, 0.1);
}

.header-content {
  max-width: 1400px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 16px;
  cursor: pointer;
}

.logo-circle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid var(--brand-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  background: rgba(0, 255, 209, 0.05);
  transition: var(--transition-smooth);
}

.logo-circle:hover {
  border-color: var(--brand-active);
  background: rgba(0, 255, 209, 0.15);
  transform: scale(1.05);
}

.logo-circle.small {
  width: 40px;
  height: 40px;
}

.dark-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.brand-name {
  font-size: 22px;
  font-weight: 600;
  color: var(--text-primary);
  letter-spacing: -0.5px;
}

.dark-nav {
  display: flex;
  align-items: center;
  gap: 32px;
}

.dark-nav-link {
  color: var(--text-muted);
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  transition: var(--transition-fast);
  cursor: pointer;
  position: relative;
}

.dark-nav-link::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--brand-primary);
  transition: width 0.3s ease;
}

.dark-nav-link:hover {
  color: var(--text-primary);
}

.dark-nav-link:hover::after {
  width: 100%;
}

.dark-nav-link.active {
  color: var(--brand-primary);
}

.dark-nav-link.active::after {
  width: 100%;
}

/* ===== BUTTONS ===== */
.btn-primary {
  background: var(--brand-primary);
  color: #000000;
  border: none;
  border-radius: 0px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition-smooth);
  min-height: 48px;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  line-height: 1.2;
  position: relative;
  overflow: hidden;
}

.btn-primary:hover {
  background: #00E6BD;
  color: #000000;
  box-shadow: 0 8px 30px rgba(0, 255, 209, 0.5), 0 0 40px rgba(0, 255, 209, 0.3);
  transform: translateY(-3px) scale(1.02);
}

.btn-primary:active {
  background: #00CCB0;
  transform: translateY(-1px) scale(1.01);
}

.btn-primary.btn-large {
  padding: 18px 32px;
  font-size: 18px;
  min-height: 56px;
  gap: 12px;
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-primary);
  border: none;
  border-radius: 0px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition-smooth);
  min-height: 48px;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  line-height: 1.2;
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.2);
  color: var(--text-primary);
  box-shadow: 0 8px 25px rgba(255, 255, 255, 0.2);
  transform: translateY(-3px) scale(1.02);
}

.btn-secondary:active {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-1px) scale(1.01);
}

.btn-secondary.btn-large {
  padding: 18px 32px;
  font-size: 18px;
  min-height: 56px;
}

/* ===== HERO SECTION ===== */
.hero-section {
  min-height: calc(100vh - 80px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-2xl) 7.6923%;
  position: relative;
  overflow: hidden;
}

.hero-content {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 2;
}

.diplomatic-quote {
  margin-bottom: var(--spacing-xl);
  position: relative;
}

.quote-mark {
  font-size: 120px;
  line-height: 1;
  color: var(--text-silver);
  opacity: 0.3;
  font-family: Georgia, serif;
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
}

.quote-mark.closing {
  top: auto;
  bottom: -60px;
  transform: translateX(-50%) rotate(180deg);
}

.quote-text {
  font-size: 28px;
  line-height: 1.6;
  color: var(--text-silver);
  font-weight: 400;
  font-style: italic;
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 0;
}

.hero-brand {
  margin: var(--spacing-xl) 0;
}

.brand-title {
  font-size: 72px;
  font-weight: 700;
  letter-spacing: -2px;
  background: linear-gradient(135deg, var(--text-primary) 0%, var(--brand-primary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: var(--spacing-md);
}

.brand-tagline {
  font-size: 22px;
  color: var(--text-secondary);
  font-weight: 400;
  max-width: 600px;
  margin: 0 auto;
}

.hero-cta {
  display: flex;
  gap: var(--spacing-md);
  justify-content: center;
  align-items: center;
  margin-top: var(--spacing-xl);
}

.hero-gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 50%, rgba(0, 255, 209, 0.05) 0%, transparent 70%);
  pointer-events: none;
}

/* ===== SECTION STYLES ===== */
.section-container {
  padding: var(--spacing-2xl) 7.6923%;
  position: relative;
}

.section-container.section-dark {
  background: rgba(10, 14, 39, 0.3);
  border-top: 1px solid var(--border-subtle);
  border-bottom: 1px solid var(--border-subtle);
}

.section-content {
  max-width: 1200px;
  margin: 0 auto;
}

.section-header {
  text-align: center;
  margin-bottom: var(--spacing-xl);
}

.section-label {
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--brand-primary);
  margin-bottom: var(--spacing-sm);
}

.section-title {
  font-size: 48px;
  font-weight: 600;
  letter-spacing: -1px;
  color: var(--text-primary);
  margin-bottom: var(--spacing-md);
}

.section-intro {
  text-align: center;
  max-width: 800px;
  margin: 0 auto var(--spacing-xl) auto;
}

.section-closing {
  text-align: center;
  max-width: 800px;
  margin: var(--spacing-xl) auto 0 auto;
}

/* ===== ABOUT SECTION ===== */
.about-content {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.about-content p {
  margin-bottom: var(--spacing-lg);
}

.about-content p:last-child {
  margin-bottom: 0;
}

/* ===== WHY CHOOSE US ===== */
.why-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--spacing-lg);
  margin: var(--spacing-xl) 0;
}

.why-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid var(--border-subtle);
  padding: var(--spacing-lg);
  transition: all 0.3s ease;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease forwards;
}

.why-card:nth-child(1) {
  animation-delay: 0.1s;
}

.why-card:nth-child(2) {
  animation-delay: 0.2s;
}

.why-card:nth-child(3) {
  animation-delay: 0.3s;
}

.why-card:nth-child(4) {
  animation-delay: 0.4s;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.why-card:hover {
  background: rgba(255, 255, 255, 0.06);
  border-color: var(--brand-primary);
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 255, 209, 0.2);
}

.why-card h3 {
  color: var(--brand-primary);
  margin-bottom: var(--spacing-sm);
  transition: color 0.3s ease;
}

.why-card:hover h3 {
  color: var(--brand-active);
}

/* ===== SERVICES GRID ===== */
.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--spacing-lg);
  margin: var(--spacing-xl) 0;
}

.service-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid var(--border-subtle);
  padding: var(--spacing-lg);
  transition: var(--transition-smooth);
}

.service-card:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--border-medium);
  transform: translateY(-4px);
  box-shadow: 0 8px 32px rgba(0, 255, 209, 0.1);
}

.service-icon {
  color: var(--brand-primary);
  margin-bottom: var(--spacing-md);
  display: flex;
  align-items: center;
}

.service-card h3 {
  margin-bottom: var(--spacing-sm);
}

/* ===== PORTFOLIO GRID ===== */
.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--spacing-lg);
  margin: var(--spacing-xl) 0;
}

.portfolio-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid var(--border-subtle);
  overflow: hidden;
  transition: all 0.3s ease;
}

.portfolio-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 40px rgba(0, 255, 209, 0.25);
  border-color: var(--brand-primary);
}

.portfolio-image {
  width: 100%;
  height: 250px;
  position: relative;
  overflow: hidden;
}

.portfolio-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.portfolio-card:hover .portfolio-image img {
  transform: scale(1.1);
}

.portfolio-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, transparent 0%, rgba(0, 0, 0, 0.8) 100%);
  display: flex;
  align-items: flex-end;
  padding: var(--spacing-md);
  transition: background 0.3s ease;
}

.portfolio-card:hover .portfolio-overlay {
  background: linear-gradient(180deg, rgba(0, 255, 209, 0.1) 0%, rgba(0, 0, 0, 0.9) 100%);
}

.portfolio-category {
  font-size: 14px;
  font-weight: 500;
  color: var(--brand-primary);
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: color 0.3s ease;
}

.portfolio-card:hover .portfolio-category {
  color: var(--text-primary);
  text-shadow: 0 0 10px rgba(0, 255, 209, 0.5);
}

.portfolio-content {
  padding: var(--spacing-md);
}

.portfolio-content h3 {
  margin-bottom: var(--spacing-sm);
  transition: color 0.3s ease;
}

.portfolio-card:hover .portfolio-content h3 {
  color: var(--brand-primary);
}

/* ===== CONTACT SECTION - MINIMAL ICON ONLY ===== */
.contact-section {
  padding: var(--spacing-2xl) 7.6923% var(--spacing-xl) 7.6923%;
}

.contact-grid-minimal {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-xl);
  margin: var(--spacing-xl) 0;
  flex-wrap: wrap;
}

.contact-icon-only {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--border-subtle);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--brand-primary);
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
}

.contact-icon-only::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: var(--brand-primary);
  opacity: 0;
  transform: scale(0.8);
  transition: all 0.3s ease;
}

.contact-icon-only:hover {
  background: rgba(0, 255, 209, 0.15);
  border-color: var(--brand-primary);
  transform: translateY(-8px) scale(1.15);
  box-shadow: 0 15px 45px rgba(0, 255, 209, 0.35);
}

.contact-icon-only:hover::before {
  opacity: 0.1;
  transform: scale(1);
}

.contact-icon-only:active {
  transform: translateY(-6px) scale(1.1);
}

.contact-icon-only.whatsapp-icon-only {
  color: var(--whatsapp-green);
}

.contact-icon-only.whatsapp-icon-only::before {
  background: var(--whatsapp-green);
}

.contact-icon-only.whatsapp-icon-only:hover {
  background: rgba(37, 211, 102, 0.15);
  border-color: var(--whatsapp-green);
  box-shadow: 0 15px 45px rgba(37, 211, 102, 0.4);
}

.contact-icon-only.instagram-icon-only {
  color: #E4405F;
}

.contact-icon-only.instagram-icon-only::before {
  background: #E4405F;
}

.contact-icon-only.instagram-icon-only:hover {
  background: rgba(228, 64, 95, 0.15);
  border-color: #E4405F;
  box-shadow: 0 15px 45px rgba(228, 64, 95, 0.4);
}

/* ===== FOOTER ===== */
.footer {
  background: rgba(0, 0, 0, 0.5);
  border-top: 1px solid var(--border-subtle);
  padding: var(--spacing-xl) 7.6923%;
  opacity: 0;
  transform: translateY(30px);
  animation: footerFadeIn 0.8s ease forwards;
  animation-delay: 0.2s;
}

@keyframes footerFadeIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.footer-brand {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-bottom: var(--spacing-md);
  transition: transform 0.3s ease;
}

.footer-brand:hover {
  transform: scale(1.05);
}

.footer-brand-name {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
}

.footer-tagline {
  font-size: 16px;
  color: var(--text-secondary);
  margin-bottom: var(--spacing-md);
  transition: color 0.3s ease;
}

.footer-tagline:hover {
  color: var(--brand-primary);
}

.footer-links {
  margin: var(--spacing-md) 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.footer-separator {
  color: var(--text-muted);
  font-size: 14px;
}

.footer-link {
  font-size: 14px;
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  padding-bottom: 2px;
}

.footer-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background: var(--brand-primary);
  transition: width 0.3s ease;
}

.footer-link:hover {
  color: var(--brand-primary);
}

.footer-link:hover::after {
  width: 100%;
}

.footer-copyright {
  font-size: 14px;
  color: var(--text-muted);
}

/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 1024px) {
  .dark-header {
    padding: 0 5%;
  }
  
  .hero-section,
  .section-container {
    padding-left: 5%;
    padding-right: 5%;
  }
  
  .services-grid,
  .portfolio-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}

@media (max-width: 768px) {
  .dark-header {
    padding: 0 20px;
    height: 70px;
  }
  
  .dark-container {
    padding-top: 70px;
  }
  
  .dark-nav {
    display: none;
  }
  
  .brand-name {
    font-size: 18px;
  }
  
  .logo-circle {
    width: 40px;
    height: 40px;
  }
  
  .hero-section {
    min-height: auto;
    padding: var(--spacing-xl) 20px;
  }
  
  .quote-text {
    font-size: 20px;
    padding: 20px 0;
  }
  
  .quote-mark {
    font-size: 80px;
    top: -20px;
  }
  
  .quote-mark.closing {
    bottom: -40px;
  }
  
  .brand-title {
    font-size: 48px;
  }
  
  .brand-tagline {
    font-size: 18px;
  }
  
  .hero-cta {
    flex-direction: column;
    width: 100%;
  }
  
  .hero-cta .btn-primary,
  .hero-cta .btn-secondary {
    width: 100%;
  }
  
  .section-container {
    padding: var(--spacing-xl) 20px;
  }
  
  .section-title {
    font-size: 32px;
  }
  
  .body-large {
    font-size: 18px;
  }
  
  .body-medium {
    font-size: 16px;
  }
  
  .services-grid,
  .portfolio-grid,
  .why-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }
  
  .contact-grid-minimal {
    gap: var(--spacing-lg);
  }
  
  .contact-icon-only {
    width: 70px;
    height: 70px;
  }
  
  .footer {
    padding: var(--spacing-lg) 20px;
  }
}

@media (max-width: 480px) {
  .brand-title {
    font-size: 36px;
  }
  
  .quote-text {
    font-size: 18px;
  }
  
  .section-title {
    font-size: 28px;
  }
  
  .btn-primary.btn-large,
  .btn-secondary.btn-large {
    padding: 16px 24px;
    font-size: 16px;
  }
  
  .contact-grid-minimal {
    gap: var(--spacing-md);
  }
  
  .contact-icon-only {
    width: 65px;
    height: 65px;
  }
}

/* ===== PRIVACY POLICY PAGE ===== */
.privacy-policy-page {
  background: linear-gradient(180deg, #000000 0%, #0a0e27 50%, #000000 100%);
  min-height: 100vh;
  color: var(--text-primary);
}

.privacy-header {
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(12px);
  border-bottom: 1px solid var(--border-subtle);
  padding: 0 7.6923%;
  position: fixed;
  top: 0;
  width: 100%;
  height: 80px;
  z-index: 1000;
}

.privacy-header-content {
  max-width: 1400px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.back-button {
  display: flex;
  align-items: center;
  gap: 8px;
  background: transparent;
  border: 1px solid var(--border-subtle);
  color: var(--text-primary);
  padding: 10px 20px;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 0px;
}

.back-button:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--brand-primary);
  color: var(--brand-primary);
}

.privacy-logo-container {
  display: flex;
  align-items: center;
  gap: 16px;
}

.privacy-content-wrapper {
  padding: 120px 7.6923% 80px 7.6923%;
}

.privacy-content {
  max-width: 900px;
  margin: 0 auto;
  animation: fadeInUp 0.8s ease;
}

.privacy-title {
  font-size: 56px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: var(--spacing-md);
  text-align: center;
  background: linear-gradient(135deg, var(--text-primary) 0%, var(--brand-primary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.privacy-updated {
  text-align: center;
  font-size: 16px;
  color: var(--text-muted);
  margin-bottom: var(--spacing-xl);
  font-style: italic;
}

.privacy-intro {
  background: rgba(0, 255, 209, 0.05);
  border-left: 4px solid var(--brand-primary);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-2xl);
  border-radius: 0px;
}

.privacy-intro p {
  font-size: 18px;
  line-height: 1.8;
  color: var(--text-secondary);
  margin: 0;
}

.privacy-section {
  margin-bottom: var(--spacing-2xl);
  padding-bottom: var(--spacing-xl);
  border-bottom: 1px solid var(--border-subtle);
}

.privacy-section:last-of-type {
  border-bottom: none;
}

.privacy-section h2 {
  font-size: 28px;
  font-weight: 600;
  color: var(--brand-primary);
  margin-bottom: var(--spacing-lg);
  letter-spacing: -0.5px;
}

.privacy-section p {
  font-size: 17px;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: var(--spacing-md);
}

.privacy-section ul {
  list-style: none;
  padding-left: 0;
  margin: var(--spacing-md) 0;
}

.privacy-section ul li {
  font-size: 17px;
  line-height: 1.8;
  color: var(--text-secondary);
  padding-left: 30px;
  margin-bottom: var(--spacing-sm);
  position: relative;
}

.privacy-section ul li::before {
  content: 'â†’';
  position: absolute;
  left: 0;
  color: var(--brand-primary);
  font-weight: 600;
}

.privacy-closing {
  background: rgba(0, 255, 209, 0.05);
  border: 1px solid var(--brand-primary);
  padding: var(--spacing-xl);
  margin: var(--spacing-2xl) 0;
  text-align: center;
  border-radius: 0px;
}

.privacy-closing h2 {
  font-size: 28px;
  font-weight: 600;
  color: var(--brand-primary);
  margin-bottom: var(--spacing-md);
}

.privacy-closing p {
  font-size: 18px;
  line-height: 1.8;
  color: var(--text-primary);
  margin-bottom: var(--spacing-sm);
}

.privacy-closing p:last-child {
  margin-bottom: 0;
  font-weight: 500;
}

.privacy-footer-nav {
  text-align: center;
  margin-top: var(--spacing-2xl);
}

.privacy-footer-nav .btn-primary {
  display: inline-flex;
}

/* Privacy Policy Mobile Responsive */
@media (max-width: 768px) {
  .privacy-header {
    padding: 0 20px;
    height: 70px;
  }
  
  .privacy-header-content {
    flex-direction: column;
    justify-content: center;
    gap: 10px;
  }
  
  .back-button {
    font-size: 14px;
    padding: 8px 16px;
  }
  
  .privacy-logo-container .brand-name {
    font-size: 18px;
  }
  
  .privacy-content-wrapper {
    padding: 100px 20px 60px 20px;
  }
  
  .privacy-title {
    font-size: 36px;
  }
  
  .privacy-updated {
    font-size: 14px;
  }
  
  .privacy-intro {
    padding: var(--spacing-md);
  }
  
  .privacy-intro p {
    font-size: 16px;
  }
  
  .privacy-section h2 {
    font-size: 24px;
  }
  
  .privacy-section p,
  .privacy-section ul li {
    font-size: 16px;
  }
  
  .privacy-closing {
    padding: var(--spacing-lg);
  }
  
  .privacy-closing h2 {
    font-size: 24px;
  }
  
  .privacy-closing p {
    font-size: 16px;
  }
}
